<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">课程表</p>
            <p class="navBtn f24 cfff pr-30" @click="changeThisSchedule">本班课表</p>
        </div>
        <div class="schedule">
            <van-tabs color="#6b77ff" swipeable>
                <van-tab :title="item" v-for="(item,index) in day" :key="index">
                    <div class="item-for">
                        <div class="item-list" v-for="(items,indexs) in data" :key="indexs" v-if="items[key[index]]">
                            <div class="item-left l" v-if="items[key[index]]">
                                <div class="item-time f18 c333 mt-36">{{items.startTime}}-{{items.endTime}}</div>
                                <div class="item-num f20 c999 mt-10">第{{num[indexs]}}节</div>
                            </div>
                            <div class="item-right l" v-if="items[key[index]]">
                                <div class="classType f22 c333">{{items[key[index]].courName}}<span class="item-user ml-20">({{items[key[index]].teacherName}})</span></div>
                            </div>
                        </div>
                    </div>
                </van-tab>  
            </van-tabs>
        </div>
    </div>
</template>


<script>
export default {    
  name: 'HelloWorld',
    data () {
        return {
            day:["星期一","星期二","星期三","星期四","星期五","星期六","星期日"],
            num:['一','二','三','四','五','六','七','八','九','十','十一','十二'],
            key:['monday','tuesday','wednesday','thursday','friday','saturday','sunday'],
            data:[]
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'})
        },
        changeThisSchedule(){
            this.$router.push({path:'/classschedule'})
        },
        getClassschedule(){//获取一周的课表
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.classschedule.classschedule},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId,teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err =>{
                this.$toast(data.msg);
            })
        }
    },
    created() {
        this.getClassschedule();
        // this.data = JSON.parse(localStorage.getItem('json'))
        // console.log(this.data)
    },
}
</script>

<style scoped>
.loginNavFix{z-index: 99999;}
.navBtn{width: 1rem;}
.schedule{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;padding:0 0.3rem;box-sizing: border-box;}
.item-for{width: 100%;overflow: hidden;}
.item-list{border-bottom: 1px solid #f2f2f2;height: 1.4rem;text-align: center;overflow: hidden;}
.item-left{width: 2.6rem;border-right: 1px solid #f2f2f2;height: 100%; }
.item-right{width: 4.25rem;height: 100%;line-height: 1.4rem;overflow: hidden;}
.item-user{color: #adadad !important;}
</style>






